//  Step related variable Begins
.stepBulletLabel() {
	@transform: translateX(calc(50% - 13px));
}
.stepFlatBody() {
	@padding: 6px 30px 6px 15px;
}
.stepAdvancedStepItemAfterMixin() {
	@transform: rotate(-135deg) skew(15deg, 15deg);
}
.stepAdvancedStepBackwardBeforeMixin() {
	@transform: rotate(-135deg);
}
//  Step related variable Ends

//  Table related variable Begins
.TableFixed() {
    @box-shadow: -3px 0 7px -2px @tableFixedBoxShadow;
}
//  Table related variable Ends

//	Timepicker related variable Begins
.lytetimePickerButtonMixin() {
	@border-radius: 1px 0 0 1px;
}
//	Timepicker related variable Ends

//  Tooltip related variable Begins
.tooltipInnerSpan() {
    @transform: translateX(5px) rotate(45deg);
}
.whiteTooltipBeforeRight() {
    @box-shadow: 1px -1px 0 0 @alternateTooltipBorder;
}
.whiteTooltipBeforeLeft() {
    @box-shadow: -1px 1px 0 0 @alternateTooltipBorder;
}
//  Tooltip related variable Ends

